import * as React from 'react';
import { HorizontalScrollGridLayout, SectionHeader, StickyFooterLayout } from '@miq/componentjs';
import { SharedDataCtx } from '@miq/contextjs';
import { useParams } from 'react-router-dom';

import { CategoryLinks } from './components';
import { Inputs } from '@miq/formjs';
import { getCN } from '@miq/utiljs';

// ?color=124&utm_campaign=likeshopme&utm_medium=socialmedia&utm_source=instagram&utm_content=www.instagram.com/p/../
// ?utm_campaign=linktreeshopig&utm_source=instabio&utm_medium=social

export default function LIBCreateView() {
  const [q, setQ] = React.useState('');
  const { libName: utm_campaign } = useParams();
  const { lib_data = {}, site = {}, categories = [], cart = {}, recent = [] } = React.useContext(SharedDataCtx);

  console.log(recent);

  const { utm_content, utm_medium, utm_source } = lib_data;

  return (
    <div>
      <StickyFooterLayout
        className="lib-createview"
        mainCN="lib-createview-content"
        header={
          <div className="lib-createview-header miq-container center p-0">
            <SectionHeader title={site?.name} />
            <div className="py-1 border-bottom">
              <form className={getCN(['p-search-form', 'px-1'])} action={`/shop/`}>
                <Inputs.SearchInput
                  required
                  value={q}
                  onChange={(e) => {
                    setQ(e.target.value);
                  }}
                  placeholder="Cherchez un produit ..."
                />
                <input type="hidden" name="utm_campaign" value={utm_campaign} />
                {utm_medium && <input type="hidden" name="utm_medium" value={utm_medium} />}
                {utm_source && <input type="hidden" name="utm_source" value={utm_source} />}
                {utm_content && <input type="hidden" name="utm_content" value={utm_content} />}
              </form>
            </div>
            <div className="py-1">
              <HorizontalScrollGridLayout className="cat-grid">
                {categories?.map((cat: any) => {
                  return (
                    <div className="item" key={cat.meta_slug}>
                      <a href={cat.url} className="name" title={cat.name}>
                        {cat.name_truncated}
                      </a>
                    </div>
                  );
                })}
              </HorizontalScrollGridLayout>
            </div>
          </div>
        }
        footer={
          <div className="text-center p-3 pb-1 text-sm">
            with {``} by <span className="fw-bold">miQ</span>
          </div>
        }
      >
        <a href="">Continuer vos achats</a>
        <a href="/shop/">Voir la boutique</a>
        <a href="">Work with us</a>
      </StickyFooterLayout>
    </div>
  );
}
